.wrapper{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}
.container{
    width: 300px;
    height: 300px;
    background-color: rgba(50, 151, 149, 0.86);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    border: 1px solid #000;
}